export default {
	data() {
		return {
			reportInfo:{},
			houseAreaTipList:[],
			notes:[],
			yuanqiPlan:[],
		}
	},
	onShow() {
		this.getHouseAreaTipList();
		this.getReportInfo();
		this.getNotes();
		this.getYuanqiPlan();
	},
	methods: {
		// 获取报告总体信息接口
		async getReportInfo() {
			try {
				let data = await this.$wxhttp.post({
					url: '/report/v1/getReportInfo'
				})
				this.reportInfo = data.data;
			} catch (e) {
				console.log(e)
			}
		},
		// 获取偏好信息接口
		async getHouseAreaTipList() {
			try {
				let data = await this.$wxhttp.post({
					url: '/report/v1/getHouseAreaTipList'
				})
				this.houseAreaTipList = data.data;
			} catch (e) {
				console.log(e)
			}
		},
		// 获取注意事项信息接口
		async getNotes() {
			try {
				let data = await this.$wxhttp.post({
					url: '/report/v1/getNotes'
				})
				this.notes = data.data;
			} catch (e) {
				console.log(e)
			}
		},
		// 获取元气计划周期信息接口
		async getYuanqiPlan() {
			try {
				let data = await this.$wxhttp.post({
					url: '/report/v1/getYuanqiPlan'
				})
				this.yuanqiPlan = data.data;
			} catch (e) {
				console.log(e)
			}
		},
	}
}
